/* src/components/ScoreBoard.css */
.scoreboard {
    display: flex;
    justify-content: center;
    margin: 20px 0;
  }
  
  .score {
    margin: 0 20px;
  }
  
  .score h2 {
    margin: 0;
    font-size: 24px;
  }
  
  .score p {
    margin: 10px 0;
    font-size: 32px;
    font-weight: bold;
  }
  